import React, { Component } from "react";
import { updateRenderState } from ".";
import SearchPage from "./SearchPage";
import { userData, data } from "./Data";
import ItemCard from "./ItemCard";
import { S } from "./alabsi";
import CreateMeal from "./CreateMeal";
import MealDetails from "./MealDetails";
import MainPage from "./MainPage";
import NavicationBar from "./NavicationBar";

export default class Diary extends Component {
  componentDidMount() {
    this.checkEatenMeal("breakfast");
    this.checkEatenMeal("lunch");
    this.checkEatenMeal("dinner");
    this.checkEatenMeal("snacks");
  }

  componentDidUpdate() {
    this.checkEatenMeal("breakfast");
    this.checkEatenMeal("lunch");
    this.checkEatenMeal("dinner");
    this.checkEatenMeal("snacks");
  }

  checkEatenMeal = m => {
	const getData = userData.diary.filter((e) => e.date === this.props.date)[0];
    if (getData && getData.eaten && getData.eaten.includes(m)) {
      S("#mealEaten-" + m).css("background-color", "var(--main-color)").css("border", "none");
      S("#mealEaten-" + m + " svg").css("fill", "white");
    } else if (!getData || !getData.eaten || !getData.eaten.includes(m)) {
      S("#mealEaten-" + m).removeAttr("style");
      S("#mealEaten-" + m + " svg").removeAttr("style");
	};
  };

  eatenMeal = (m) => {
    const getData = userData.diary.filter((e) => e.date === this.props.date)[0];
    if (!getData.eaten) getData.eaten = [];
    if (getData.eaten.includes(m)) {
      S("#mealEaten-" + m).removeAttr("style");
      S("#mealEaten-" + m + " svg").removeAttr("style");
      getData.eaten.splice(getData.eaten.indexOf(m), 1);
      if (getData.eaten.length === 0) delete getData.eaten;
    } else {
      S("#mealEaten-" + m)
        .css("background-color", "var(--main-color)")
        .css("border", "none");
      S("#mealEaten-" + m + " svg").css("fill", "white");
      getData.eaten.push(m);
    }
    window.localStorage.setItem("userData", JSON.stringify(userData));
  };

  mountSearchComponent = (ar, en) => {
    updateRenderState(<SearchPage key={Math.random() * 100} title={ar} doAnimation={true} />);
    window.history.pushState("", "", "/search#" + en);
  };

  mealKcalSum = (mealName) => {
    const getDataByDate = userData.diary.filter((e) => e.date === this.props.date)[0];
    if (getDataByDate && getDataByDate[mealName]) {
      const getDataById = getDataByDate[mealName].map(
        (e) => (e = Object.assign({}, data.filter((el) => el.id === e[0])[0], { value: e[1], input: e[2] }))
      );
      return Math.round(getDataById.map((e) => e.kcal * ((e.value * e.input) / 100)).reduce((a, b) => a + b));
    }
  };

  renderMeal = (mealName) => {
    const getDataByDate = userData.diary.filter((e) => e.date === this.props.date)[0];
    if (getDataByDate && getDataByDate[mealName]) {
      const getDataById = getDataByDate[mealName].map(
        (e) => (e = Object.assign({}, data.filter((el) => el.id === e[0])[0], { value: e[1], input: e[2] }))
      );
      return getDataById.map((e) => (
        <ItemCard
          key={e.id + Math.random() * 1000}
          name={e.name}
          kcal={Math.round((e.kcal / 100) * (e.value * e.input))}
          serving={
            e.value === 1 ? e.input + " غرام " : e.input + "  " + e.serving.filter((el) => el[1] === e.value)[0][0]
          }
          id={e.id}
          description={e.description}
          value={e.value}
          input={e.input}
          mealName={mealName}
        />
      ));
    }
  };

  mealDotsHandle = (e) => {
    const getMealName = e.target.id.split("P")[0];
    const popId = "#" + getMealName + "PopUp";
    const isScale1 = S(popId).getStyle("transform").includes("1");
    S(".mealPopUp").css("transform", "scale(0)");
    if (isScale1) {
      S(popId).css("transform", "scale(0)");
    } else {
      S(popId).css("transform", "scale(1)");
      document.addEventListener(
        "click",
        (e) => {
          if (
            e.target.className !== "newMealPopUp" &&
            e.target.id !== "breakfastPopUpSvg" &&
            e.target.id !== "lunchPopUpSvg" &&
            e.target.id !== "dinnerPopUpSvg" &&
            e.target.id !== "snacksPopUpSvg"
          ) {
            S(".mealPopUp").css("transform", "scale(0)");
          }
        },
        { once: true }
      );
    }
  };

  createMeal = (mealName) => {
    const getData = [...userData.diary.filter((e) => e.date === this.props.date)[0][mealName]];
    userData.favorite.tmpMeal.mealContent = getData;
    userData.favorite.tmpMeal.mealName = "";
    updateRenderState(<CreateMeal key={Math.random() * 100} doAnimation={true} goBackOnce={true} />);
    window.history.pushState("", "", "/CreateNewMeal");
  };

  deleteAll = (m) => {
    const getDataByDate = userData.diary.filter((e) => e.date === this.props.date)[0];
    delete getDataByDate[m];
    updateRenderState([
      <MainPage key={Math.random() * 100} doAnimation={false} />,
      <NavicationBar key={Math.random() * 100} />,
    ]);
    window.localStorage.setItem("userData", JSON.stringify(userData));
  };

  render() {
    return (
      <div id="pageBody">
        <div className="mealName">
          <h3>الفطور</h3>
          <svg
            style={{ display: this.renderMeal("breakfast") ? "block" : "none" }}
            onClick={this.mealDotsHandle}
            id="breakfastPopUpSvg"
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 24 24"
          >
            <path
              id="breakfastPopUpSvg"
              d="M12 18c1.657 0 3 1.343 3 3s-1.343 3-3 3-3-1.343-3-3 1.343-3 3-3zm0-9c1.657 0 3 1.343 3 3s-1.343 3-3 3-3-1.343-3-3 1.343-3 3-3zm0-9c1.657 0 3 1.343 3 3s-1.343 3-3 3-3-1.343-3-3 1.343-3 3-3z"
            />
          </svg>
          <div className="mealPopUp" id="breakfastPopUp">
            <p onClick={() => this.createMeal("breakfast")} className="newMealPopUp">
              إنشاء وجبة
            </p>
            <p
              className="newMealPopUp"
              onClick={() => {
                updateRenderState(
                  <MealDetails key={Math.random() * 100} mealName="breakfast" date={this.props.date} />
                );
                window.history.pushState("", "", "/MealDetails");
              }}
            >
              تفاصيل الوجبة
            </p>
            <p className="newMealPopUp" onClick={() => this.deleteAll("breakfast")}>
              حذف المحتويات
            </p>
          </div>
        </div>
        <div className="mealFoods">
          <div
            className="mealFoodsImgP"
            onClick={() => this.mountSearchComponent("الفطور", "breakfast")}
            style={{ display: this.renderMeal("breakfast") ? "none" : "block" }}
          >
            <img src="./assets/bread.png" alt="bread" />
            <p className="addYourFoodText">أضف طعامك</p>
          </div>
          {this.renderMeal("breakfast")}
          <div className="mealKcalSum" style={{ display: this.renderMeal("breakfast") ? "flex" : "none" }}>
            <p>{this.mealKcalSum("breakfast")} سعرة حرارية</p>
            <div id="mealEaten-breakfast" className="mealEaten" onClick={() => this.eatenMeal("breakfast")}>
              <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24">
                <path d="M21 6.285l-11.16 12.733-6.84-6.018 1.319-1.49 5.341 4.686 9.865-11.196 1.475 1.285z" />
              </svg>
            </div>
          </div>
        </div>
        <div className="mealName">
          <h3>الغداء</h3>
          <svg
            style={{ display: this.renderMeal("lunch") ? "block" : "none" }}
            onClick={this.mealDotsHandle}
            id="lunchPopUpSvg"
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 24 24"
          >
            <path
              id="lunchPopUpSvg"
              d="M12 18c1.657 0 3 1.343 3 3s-1.343 3-3 3-3-1.343-3-3 1.343-3 3-3zm0-9c1.657 0 3 1.343 3 3s-1.343 3-3 3-3-1.343-3-3 1.343-3 3-3zm0-9c1.657 0 3 1.343 3 3s-1.343 3-3 3-3-1.343-3-3 1.343-3 3-3z"
            />
          </svg>
          <div className="mealPopUp" id="lunchPopUp">
            <p onClick={() => this.createMeal("lunch")} className="newMealPopUp">
              إنشاء وجبة
            </p>
            <p
              className="newMealPopUp"
              onClick={() => {
                updateRenderState(<MealDetails key={Math.random() * 100} mealName="lunch" date={this.props.date} />);
                window.history.pushState("", "", "/MealDetails");
              }}
            >
              تفاصيل الوجبة
            </p>
            <p className="newMealPopUp" onClick={() => this.deleteAll("lunch")}>
              حذف المحتويات
            </p>
          </div>
        </div>
        <div className="mealFoods">
          <div
            className="mealFoodsImgP"
            onClick={() => this.mountSearchComponent("الغداء", "lunch")}
            style={{ display: this.renderMeal("lunch") ? "none" : "block" }}
          >
            <img src="./assets/soup.png" alt="soup" />
            <p className="addYourFoodText">أضف طعامك</p>
          </div>
          {this.renderMeal("lunch")}
          <div className="mealKcalSum" style={{ display: this.renderMeal("lunch") ? "flex" : "none" }}>
            <p>{this.mealKcalSum("lunch")} سعرة حرارية</p>
            <div id="mealEaten-lunch" className="mealEaten" onClick={() => this.eatenMeal("lunch")}>
              <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24">
                <path d="M21 6.285l-11.16 12.733-6.84-6.018 1.319-1.49 5.341 4.686 9.865-11.196 1.475 1.285z" />
              </svg>
            </div>
          </div>
        </div>
        <div className="mealName">
          <h3>العشاء</h3>
          <svg
            style={{ display: this.renderMeal("dinner") ? "block" : "none" }}
            onClick={this.mealDotsHandle}
            id="dinnerPopUpSvg"
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 24 24"
          >
            <path
              id="dinnerPopUpSvg"
              d="M12 18c1.657 0 3 1.343 3 3s-1.343 3-3 3-3-1.343-3-3 1.343-3 3-3zm0-9c1.657 0 3 1.343 3 3s-1.343 3-3 3-3-1.343-3-3 1.343-3 3-3zm0-9c1.657 0 3 1.343 3 3s-1.343 3-3 3-3-1.343-3-3 1.343-3 3-3z"
            />
          </svg>
          <div className="mealPopUp" id="dinnerPopUp">
            <p onClick={() => this.createMeal("dinner")} className="newMealPopUp">
              إنشاء وجبة
            </p>
            <p
              className="newMealPopUp"
              onClick={() => {
                updateRenderState(<MealDetails key={Math.random() * 100} mealName="dinner" date={this.props.date} />);
                window.history.pushState("", "", "/MealDetails");
              }}
            >
              تفاصيل الوجبة
            </p>
            <p className="newMealPopUp" onClick={() => this.deleteAll("dinner")}>
              حذف المحتويات
            </p>
          </div>
        </div>
        <div className="mealFoods">
          <div
            className="mealFoodsImgP"
            onClick={() => this.mountSearchComponent("العشاء", "dinner")}
            style={{ display: this.renderMeal("dinner") ? "none" : "block" }}
          >
            <img src="./assets/fish.png" alt="fish" />
            <p className="addYourFoodText">أضف طعامك</p>
          </div>
          {this.renderMeal("dinner")}
          <div className="mealKcalSum" style={{ display: this.renderMeal("dinner") ? "flex" : "none" }}>
            <p>{this.mealKcalSum("dinner")} سعرة حرارية</p>
            <div id="mealEaten-dinner" className="mealEaten" onClick={() => this.eatenMeal("dinner")}>
              <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24">
                <path d="M21 6.285l-11.16 12.733-6.84-6.018 1.319-1.49 5.341 4.686 9.865-11.196 1.475 1.285z" />
              </svg>
            </div>
          </div>
        </div>
        <div className="mealName">
          <h3>وجبة خفيفة</h3>
          <svg
            style={{ display: this.renderMeal("snacks") ? "block" : "none" }}
            onClick={this.mealDotsHandle}
            id="snacksPopUpSvg"
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 24 24"
          >
            <path
              id="snacksPopUpSvg"
              d="M12 18c1.657 0 3 1.343 3 3s-1.343 3-3 3-3-1.343-3-3 1.343-3 3-3zm0-9c1.657 0 3 1.343 3 3s-1.343 3-3 3-3-1.343-3-3 1.343-3 3-3zm0-9c1.657 0 3 1.343 3 3s-1.343 3-3 3-3-1.343-3-3 1.343-3 3-3z"
            />
          </svg>
          <div className="mealPopUp" id="snacksPopUp">
            <p onClick={() => this.createMeal("snacks")} className="newMealPopUp">
              إنشاء وجبة
            </p>
            <p
              className="newMealPopUp"
              onClick={() => {
                updateRenderState(<MealDetails key={Math.random() * 100} mealName="snacks" date={this.props.date} />);
                window.history.pushState("", "", "/MealDetails");
              }}
            >
              تفاصيل الوجبة
            </p>
            <p className="newMealPopUp" onClick={() => this.deleteAll("snacks")}>
              حذف المحتويات
            </p>
          </div>
        </div>
        <div className="mealFoods">
          <div
            className="mealFoodsImgP"
            onClick={(_) => this.mountSearchComponent("وجبة خفيفة", "snacks")}
            style={{ display: this.renderMeal("snacks") ? "none" : "block" }}
          >
            <img src="./assets/banana.png" alt="banana" />
            <p className="addYourFoodText">أضف طعامك</p>
          </div>
          {this.renderMeal("snacks")}
          <div className="mealKcalSum" style={{ display: this.renderMeal("snacks") ? "flex" : "none" }}>
            <p>{this.mealKcalSum("snacks")} سعرة حرارية</p>
            <div id="mealEaten-snacks" className="mealEaten" onClick={() => this.eatenMeal("snacks")}>
              <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24">
                <path d="M21 6.285l-11.16 12.733-6.84-6.018 1.319-1.49 5.341 4.686 9.865-11.196 1.475 1.285z" />
              </svg>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
