import React, { Component } from "react";
import { userData } from "./Data";
import { S, wait } from "./alabsi";

export default class Settings extends Component {

  componentDidMount() {
    this.introPageFadeTransition();
    window.scrollTo({ top: 0 });
    this.retrieveInfo();

    if (window.localStorage.getItem("tips") === "true") {
      this.moveSwitch("on", "tips");
    }

    if (window.localStorage.getItem("darkMode") === "true") {
      this.moveSwitch("on", "darkmode");
      this.autoDarkModeAnimation("show");
    }

    if (window.localStorage.getItem("autoDarkMode") === "true") {
      this.moveSwitch("on", "autoDarkmode");
    }
  }

  introPageFadeTransition = async () => {
    S("#settingsPage").css("opacity", 0).css("transition", "opacity 350ms");
    await wait(20);
    S("#settingsPage").css("opacity", 1);
    await wait(350);
    S("#settingsPage").removeAttr("style");
  };

  retrieveInfo = () => {
    S("#goalWeight").val(userData.userInfo.goalWeight);
    S("#weight").val(userData.userInfo.weight);
    S("#height").val(userData.userInfo.height);
    S("#age").val(userData.userInfo.age);
    S("#gender").val(userData.userInfo.gender);
    S("#activity").val(userData.userInfo.activity);
    S("#dailykcal").val(userData.userInfo.dailykcal);

    if (userData.userInfo.goalWeight < userData.userInfo.weight) {
      S("#weightGoal").text("خسارة الوزن");
    } else if (userData.userInfo.goalWeight === userData.userInfo.weight) {
      S("#weightGoal").text("الحفاظ على الوزن");
    } else if (userData.userInfo.goalWeight > userData.userInfo.weight) {
      S("#weightGoal").text("اكتساب الوزن");
    }
    const [carbPercent, proteinPercent, fatPercent] = [
      Math.min(Math.max(Math.round(((userData.userInfo.dailyCarbs * 4) / userData.userInfo.dailykcal) * 100), 0), 100),
      Math.min(
        Math.max(Math.round(((userData.userInfo.dailyProtein * 4) / userData.userInfo.dailykcal) * 100), 0),
        100
      ),
      Math.min(Math.max(Math.round(((userData.userInfo.dailyFat * 9) / userData.userInfo.dailykcal) * 100), 0), 100),
    ];

    S("#macrosCarbPrecent")
      .text(carbPercent + "%")
      .css("width", carbPercent + "%");
    S("#macrosProteinPrecent")
      .text(proteinPercent + "%")
      .css("width", proteinPercent + "%");
    S("#macrosFatPrecent")
      .text(fatPercent + "%")
      .css("width", fatPercent + "%");

    S("#carbInput").val(carbPercent);
    S("#proteinInput").val(proteinPercent);
    S("#fatInput").val(fatPercent);

    S("#carbKcal").text(
      Math.round((userData.userInfo.dailykcal * carbPercent) / 100) + "<span class='macrosPercentSpan'> سعرة</span>"
    );
    S("#proteinKcal").text(
      Math.round((userData.userInfo.dailykcal * proteinPercent) / 100) + "<span class='macrosPercentSpan'> سعرة</span>"
    );
    S("#fatKcal").text(
      Math.round((userData.userInfo.dailykcal * fatPercent) / 100) + "<span class='macrosPercentSpan'> سعرة</span>"
    );

    S("#carbGram").text(
      Math.round((userData.userInfo.dailykcal * carbPercent) / 100 / 4) + "<span class='macrosPercentSpan'> غ</span>"
    );
    S("#proteinGram").text(
      Math.round((userData.userInfo.dailykcal * proteinPercent) / 100 / 4) + "<span class='macrosPercentSpan'> غ</span>"
    );
    S("#fatGram").text(
      Math.round((userData.userInfo.dailykcal * fatPercent) / 100 / 9) + "<span class='macrosPercentSpan'> غ</span>"
    );
  };

  HarrisBenedictBMR = () => {
    let bmr;
    if (userData.userInfo.gender === "male") {
      bmr = 66.47 + 13.75 * userData.userInfo.weight + 5.003 * userData.userInfo.height - 6.755 * userData.userInfo.age;
    } else if (userData.userInfo.gender === "female") {
      bmr = 655.1 + 9.563 * userData.userInfo.weight + 1.85 * userData.userInfo.height - 4.676 * userData.userInfo.age;
    }
    return Math.round(Math.round(bmr) / 100) * 100;
  };

  autoAssign = () => {
    if (userData.userInfo.goalWeight < userData.userInfo.weight) {
      userData.userInfo.dailykcal = this.HarrisBenedictBMR();
      if (userData.userInfo.gender === "male") {
        userData.userInfo.dailyCarbs = Math.round((this.HarrisBenedictBMR() * 0.5) / 4);
        userData.userInfo.dailyProtein = Math.round((this.HarrisBenedictBMR() * 0.3) / 4);
        userData.userInfo.dailyFat = Math.round((this.HarrisBenedictBMR() * 0.2) / 9);
      } else {
        userData.userInfo.dailyCarbs = Math.round((this.HarrisBenedictBMR() * 0.5) / 4);
        userData.userInfo.dailyProtein = Math.round((this.HarrisBenedictBMR() * 0.25) / 4);
        userData.userInfo.dailyFat = Math.round((this.HarrisBenedictBMR() * 0.25) / 9);
      }
      this.retrieveInfo();
    } else if (userData.userInfo.goalWeight === userData.userInfo.weight) {
      userData.userInfo.dailykcal = Math.round(this.HarrisBenedictBMR() * Number(S("#activity").val()));
      if (userData.userInfo.gender === "male") {
        userData.userInfo.dailyCarbs = Math.round((userData.userInfo.dailykcal * 0.5) / 4);
        userData.userInfo.dailyProtein = Math.round((userData.userInfo.dailykcal * 0.3) / 4);
        userData.userInfo.dailyFat = Math.round((userData.userInfo.dailykcal * 0.2) / 9);
      } else {
        userData.userInfo.dailyCarbs = Math.round((userData.userInfo.dailykcal * 0.5) / 4);
        userData.userInfo.dailyProtein = Math.round((userData.userInfo.dailykcal * 0.25) / 4);
        userData.userInfo.dailyFat = Math.round((userData.userInfo.dailykcal * 0.25) / 9);
      }
      this.retrieveInfo();
    } else if (userData.userInfo.goalWeight > userData.userInfo.weight) {
      userData.userInfo.dailykcal = Math.round(this.HarrisBenedictBMR() * Number(S("#activity").val()) + 500);
      if (userData.userInfo.gender === "male") {
        userData.userInfo.dailyCarbs = Math.round((userData.userInfo.dailykcal * 0.55) / 4);
        userData.userInfo.dailyProtein = Math.round((userData.userInfo.dailykcal * 0.2) / 4);
        userData.userInfo.dailyFat = Math.round((userData.userInfo.dailykcal * 0.25) / 9);
      } else {
        userData.userInfo.dailyCarbs = Math.round((userData.userInfo.dailykcal * 0.55) / 4);
        userData.userInfo.dailyProtein = Math.round((userData.userInfo.dailykcal * 0.2) / 4);
        userData.userInfo.dailyFat = Math.round((userData.userInfo.dailykcal * 0.25) / 9);
      }
      this.retrieveInfo();
    }
  };

  moveSwitch = (d, n) => {
    if (d === "on") {
      S("#" + n + "SwitchContainer")
        .css("background-color", "var(--second-color)")
        .css("border", "solid 1px var(--second-color)");
      S("#" + n + "SwitchButton")
        .css("left", "90%")
        .css("border", "solid 1px var(--second-color)");
    } else if (d === "off") {
      S("#" + n + "SwitchContainer")
        .css("background-color", "whitesmoke")
        .css("border", "solid 1px lightgray");
      S("#" + n + "SwitchButton")
        .css("left", "0px")
        .css("border", "solid 1px lightgray");
    }
  };

  tipSwitchButton = (state) => {
    if (state === "false") {
      this.moveSwitch("on", "tips");
      window.localStorage.setItem("tips", true);
    } else {
      this.moveSwitch("off", "tips");
      window.localStorage.setItem("tips", false);
    }
  };

  darkModeOnOff = (m) => {
    if (m === "on") {
      document.documentElement.style.setProperty("--background-gray", "#18191a");
      document.documentElement.style.setProperty("--background-white", "#242526");
      document.documentElement.style.setProperty("--background-kinda-white", "#3a3b3c");
      document.documentElement.style.setProperty("--text-kinda-black", "#e4e6eb");
      document.documentElement.style.setProperty("--text-gray", "#a7aaae");
    } else if (m === "off") {
      document.documentElement.style.setProperty("--background-gray", "#f5f5f5");
      document.documentElement.style.setProperty("--background-white", "white");
      document.documentElement.style.setProperty("--background-kinda-white", "#fafaf5");
      document.documentElement.style.setProperty("--text-kinda-black", "#2b2b2b");
      document.documentElement.style.setProperty("--text-gray", "#646464");
    }
  };

  darkmodeSwitchButton = (state) => {
    if (state === "false") {
      this.moveSwitch("on", "darkmode");
      this.darkModeOnOff("on");
      this.autoDarkModeAnimation("show");
      window.localStorage.setItem("darkMode", true);
    } else {
      this.moveSwitch("off", "darkmode");
      this.darkModeOnOff("off");
      this.autoDarkModeAnimation("hide");
      this.moveSwitch("off", "autoDarkmode");
      window.localStorage.setItem("darkMode", false);
      window.localStorage.setItem("autoDarkMode", false);
    }
  };

  autoDarkModeAnimation = async (state) => {
    if (state === "show") {
      const getHeight = S("#darkmodeSettingsContainer").getStyle("height", true);
      S("#darkmodeSettingsContainer").css("height", getHeight + "px");
      S("#autoDarkmodeSettingsContainer").css("display", "flex");
      await wait(10);
      S("#darkmodeSettingsContainer").css("height", getHeight * 2 + "px");
      await wait(50);
      S("#autoDarkmodeSettingsContainer").css("transform", "scaleY(1)");
    } else if (state === "hide") {
      const getHeight = S("#darkmodeSettingsContainer").getStyle("height", true);
      S("#darkmodeSettingsContainer").css("height", getHeight + "px");
      await wait(10);
      S("#autoDarkmodeSettingsContainer").css("transform", "scaleY(0)");
      S("#darkmodeSettingsContainer").css("height", getHeight / 2 + "px");
      await wait(250);
      S("#autoDarkmodeSettingsContainer").css("display", "none");
    }
  };

  autoDarkmodeSwitchButton = (state) => {
    if (state === "false") {
      this.moveSwitch("on", "autoDarkmode");
      this.showToast("من الساعة 20:00 وحتى الساعة 7:00");
      const date = new Date();
      const hour = date.getHours();
      if (hour >= 20 || hour <= 6) {
        this.darkModeOnOff("on");
      } else {
        this.darkModeOnOff("off");
      }
      window.localStorage.setItem("autoDarkMode", true);
    } else {
      this.moveSwitch("off", "autoDarkmode");
      this.darkModeOnOff("on");
      window.localStorage.setItem("autoDarkMode", false);
    }
  };

  changeThemeColor = (c1, c2) => {
    const colors = [c1, c2];
    document.documentElement.style.setProperty("--main-color", colors[0]);
    document.documentElement.style.setProperty("--second-color", colors[1]);
    document.querySelector("meta[name=theme-color]").setAttribute("content", colors[0]);
    window.localStorage.setItem("theme", JSON.stringify(colors));
  };

  showToast = async (text) => {
    S("#settingsToast").text(text).css("display", "block");
    await wait(20);
    S("#settingsToast").css("opacity", 1).css("bottom", "100px");
    await wait(2000);
    S("#settingsToast").css("opacity", 0).css("bottom", "0px");
    await wait(350);
    S("#settingsToast").removeAttr("style");
  };

  render() {
    return (
      <div id="settingsPage">
        <p id="settingsToast"></p>
        <header id="settingsPageHeader">
          <p id="settingsPageHeaderTitle">الإعــدادات</p>
        </header>
        <p className="containerTitles">المعلومات الشخصية</p>
        <div id="personalInfoContainer">
          <div className="settingsItemsContainer">
            <p className="personalInfoTitles">الهدف</p>
            <p id="weightGoal"></p>
          </div>
          <div className="settingsItemsContainer">
            <p className="personalInfoTitles">الوزن المراد تحقيقه</p>
            <p className="settingsUnits">كغ</p>
            <input
              className="personalInfoInputs"
              id="goalWeight"
              type="number"
              placeholder="كغ"
              onClick={(e) => e.target.select()}
              onChange={(e) => {
                userData.userInfo.goalWeight = Number(e.target.value);
                if (userData.userInfo.goalWeight < userData.userInfo.weight) {
                  S("#weightGoal").text("خسارة الوزن");
                } else if (userData.userInfo.goalWeight === userData.userInfo.weight) {
                  S("#weightGoal").text("الحفاظ على الوزن");
                } else if (userData.userInfo.goalWeight > userData.userInfo.weight) {
                  S("#weightGoal").text("اكتساب الوزن");
                }
              }}
            />
          </div>
          <div className="settingsItemsContainer">
            <p className="personalInfoTitles">الوزن الحالي</p>
            <p className="settingsUnits">كغ</p>
            <input
              className="personalInfoInputs"
              id="weight"
              type="number"
              placeholder="كغ"
              onClick={(e) => e.target.select()}
              onChange={(e) => {
                userData.userInfo.weight = Number(e.target.value);
                if (userData.userInfo.goalWeight < userData.userInfo.weight) {
                  S("#weightGoal").text("خسارة الوزن");
                } else if (userData.userInfo.goalWeight === userData.userInfo.weight) {
                  S("#weightGoal").text("الحفاظ على الوزن");
                } else if (userData.userInfo.goalWeight > userData.userInfo.weight) {
                  S("#weightGoal").text("اكتساب الوزن");
                }
              }}
            />
          </div>
          <div className="settingsItemsContainer">
            <p className="personalInfoTitles">الطول</p>
            <p className="settingsUnits">سم</p>
            <input
              className="personalInfoInputs"
              id="height"
              type="number"
              placeholder="سم"
              onClick={(e) => e.target.select()}
              onChange={(e) => {
                userData.userInfo.height = Number(e.target.value);
              }}
            />
          </div>
          <div className="settingsItemsContainer">
            <p className="personalInfoTitles">العمر</p>
            <p className="settingsUnits">سنة</p>
            <input
              className="personalInfoInputs"
              id="age"
              type="number"
              placeholder="سنة"
              onClick={(e) => e.target.select()}
              onChange={(e) => {
                userData.userInfo.age = Number(e.target.value);
              }}
            />
          </div>
          <div className="settingsItemsContainer">
            <p className="personalInfoTitles">الجنس</p>
            <select
              id="gender"
              className="personalInfoInputs"
              onChange={(e) => (userData.userInfo.gender = e.target.value)}
            >
              <option value="male">ذكر</option>
              <option value="female">أنثى</option>
            </select>
          </div>
          <div className="settingsItemsContainer">
            <p className="personalInfoTitles">مستوى النشاط</p>
            <select
              id="activity"
              className="personalInfoInputs"
              onChange={(e) => (userData.userInfo.activity = e.target.value)}
            >
              <option value={1.2}>كثير الجلوس</option>
              <option value={1.375}>قليل النشاط</option>
              <option value={1.55}>متوسط النشاط</option>
              <option value={1.725}>كثير النشاط</option>
              <option value={1.9}>عالي النشاط</option>
            </select>
          </div>
        </div>

        <p className="containerTitles">إعــدادت الـتـغـذيـة</p>
        <div id="macrosInfoContainer">
          <div className="settingsItemsContainer">
            <p className="personalInfoTitles">السعرات اليومية</p>
            <p className="settingsUnits">سعرة</p>
            <input
              id="dailykcal"
              className="personalInfoInputs"
              type="number"
              placeholder="سعرة"
              onClick={(e) => e.target.select()}
              onChange={(e) => {
                const dailykcal = Number(e.target.value);
                const [carbPercent, proteinPercent, fatPercent] = [
                  Number(S("#carbInput").val()),
                  Number(S("#proteinInput").val()),
                  Number(S("#fatInput").val()),
                ];
                userData.userInfo.dailykcal = dailykcal;
                userData.userInfo.dailyCarbs = Math.round((Number(e.target.value) * carbPercent) / 100 / 4);
                userData.userInfo.dailyProtein = Math.round((Number(e.target.value) * proteinPercent) / 100 / 4);
                userData.userInfo.dailyFat = Math.round((Number(e.target.value) * fatPercent) / 100 / 9);

                S("#carbKcal").text((dailykcal * carbPercent) / 100 + "<span class='macrosPercentSpan'> سعرة</span>");
                S("#proteinKcal").text(
                  (dailykcal * proteinPercent) / 100 + "<span class='macrosPercentSpan'> سعرة</span>"
                );
                S("#fatKcal").text((dailykcal * fatPercent) / 100 + "<span class='macrosPercentSpan'> سعرة</span>");

                S("#carbGram").text(
                  Math.round((dailykcal * carbPercent) / 100 / 4) + "<span class='macrosPercentSpan'> غ</span>"
                );
                S("#proteinGram").text(
                  Math.round((dailykcal * proteinPercent) / 100 / 4) + "<span class='macrosPercentSpan'> غ</span>"
                );
                S("#fatGram").text(
                  Math.round((dailykcal * fatPercent) / 100 / 9) + "<span class='macrosPercentSpan'> غ</span>"
                );
              }}
            />
          </div>
          <div id="macrosContainer">
            <div id="macrosCarbPrecent"></div>
            <div id="macrosProteinPrecent"></div>
            <div id="macrosFatPrecent"></div>
          </div>
          <div id="carbContainer">
            <p id="carbTitle" className="macrosPercentTitles">
              الكربوهيدرات
            </p>
            <div style={{ width: "15%", display: "flex" }}>
              %
              <input
                id="carbInput"
                className="macrosPercentInputs"
                type="number"
                onClick={(e) => e.target.select()}
                onChange={(e) => {
                  const maxVale = 100 - (Number(S("#proteinInput").val()) + Number(S("#fatInput").val()));
                  const val =
                    Number(e.target.value) > maxVale
                      ? maxVale
                      : Number(e.target.value) < 0
                      ? 0
                      : Number(e.target.value);
                  e.target.value = val;
                  userData.userInfo.dailyCarbs = Math.round((userData.userInfo.dailykcal * val) / 100 / 4);
                  S("#macrosCarbPrecent")
                    .text(val + "%")
                    .css("width", val + "%");
                  S("#carbKcal").text(
                    userData.userInfo.dailyCarbs * 4 + "<span class='macrosPercentSpan'> سعرة</span>"
                  );
                  S("#carbGram").text(userData.userInfo.dailyCarbs + "<span class='macrosPercentSpan'> غ</span>");
                }}
              />
            </div>
            <p id="carbKcal" className="macrosPercentKcal">
              650<span className="macrosPercentSpan"> سعرة</span>
            </p>
            <p id="carbGram" className="macrosPercentGram">
              200<span className="macrosPercentSpan"> غ</span>
            </p>
          </div>
          <div id="proteinContainer">
            <p id="proteinTitle" className="macrosPercentTitles">
              الــبـــروتــيـــن
            </p>
            <div style={{ width: "15%", display: "flex" }}>
              %
              <input
                id="proteinInput"
                className="macrosPercentInputs"
                type="number"
                onClick={(e) => e.target.select()}
                onChange={(e) => {
                  const maxVale = 100 - (Number(S("#carbInput").val()) + Number(S("#fatInput").val()));
                  const val =
                    Number(e.target.value) > maxVale
                      ? maxVale
                      : Number(e.target.value) < 0
                      ? 0
                      : Number(e.target.value);
                  e.target.value = val;
                  userData.userInfo.dailyProtein = Math.round((userData.userInfo.dailykcal * val) / 100 / 4);
                  S("#macrosProteinPrecent")
                    .text(val + "%")
                    .css("width", val + "%");
                  S("#proteinKcal").text(
                    userData.userInfo.dailyProtein * 4 + "<span class='macrosPercentSpan'> سعرة</span>"
                  );
                  S("#proteinGram").text(userData.userInfo.dailyProtein + "<span class='macrosPercentSpan'> غ</span>");
                }}
              />
            </div>
            <p id="proteinKcal" className="macrosPercentKcal">
              650<span className="macrosPercentSpan"> سعرة</span>
            </p>
            <p id="proteinGram" className="macrosPercentGram">
              200<span className="macrosPercentSpan"> غ</span>
            </p>
          </div>
          <div id="fatContainer">
            <p id="fatTitle" className="macrosPercentTitles">
              الـــــدهــــــون
            </p>
            <div style={{ width: "15%", display: "flex" }}>
              %
              <input
                id="fatInput"
                className="macrosPercentInputs"
                type="number"
                onClick={(e) => e.target.select()}
                onChange={(e) => {
                  const maxVale = 100 - (Number(S("#carbInput").val()) + Number(S("#proteinInput").val()));
                  const val =
                    Number(e.target.value) > maxVale
                      ? maxVale
                      : Number(e.target.value) < 0
                      ? 0
                      : Number(e.target.value);
                  e.target.value = val;
                  userData.userInfo.dailyFat = Math.round((userData.userInfo.dailykcal * val) / 100 / 9);
                  S("#macrosFatPrecent")
                    .text(val + "%")
                    .css("width", val + "%");
                  S("#fatKcal").text(userData.userInfo.dailyFat * 9 + "<span class='macrosPercentSpan'> سعرة</span>");
                  S("#fatGram").text(userData.userInfo.dailyFat + "<span class='macrosPercentSpan'> غ</span>");
                }}
              />
            </div>
            <p id="fatKcal" className="macrosPercentKcal">
              2000<span className="macrosPercentSpan"> سعرة</span>
            </p>
            <p id="fatGram" className="macrosPercentGram">
              200<span className="macrosPercentSpan"> غ</span>
            </p>
          </div>
          <button id="autoAssign" onClick={this.autoAssign}>
            تـعـيـيـن تـلـقـائـي
          </button>
        </div>

        <p className="containerTitles">إعــدادت الـتـطـبـيـق</p>
        <div id="appSettingsContainer">
          <div id="tipsSettingsContainer" onClick={() => this.tipSwitchButton(window.localStorage.getItem("tips"))}>
            <p>إظــهــار نــصــائــح عــامــة</p>
            <div id="tipsSwitchContainer">
              <div id="tipsSwitchButton"></div>
            </div>
          </div>

          <div id="darkmodeSettingsContainer">
            <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
              <p>تـشغـيـل الـوضـع الـلـيـلـي</p>
              <div
                id="darkmodeSwitchContainer"
                onClick={() => this.darkmodeSwitchButton(window.localStorage.getItem("darkMode"))}
              >
                <div id="darkmodeSwitchButton"></div>
              </div>
            </div>
            <div id="autoDarkmodeSettingsContainer">
              <p>تغعيل تلقائي للوضـع الليلي</p>
              <div
                id="autoDarkmodeSwitchContainer"
                onClick={() => this.autoDarkmodeSwitchButton(window.localStorage.getItem("autoDarkMode"))}
              >
                <div id="autoDarkmodeSwitchButton"></div>
              </div>
            </div>
          </div>

          <div className="settingsItemsContainer" style={{ paddingTop: "3%" }}>
            <p className="personalInfoTitles">اخــتــر ألـوان الـتـطـبـيـق</p>
            <div
              className="themeColorContainer"
              style={{ background: "linear-gradient(to right, #41cd8c, #45d0dd)" }}
              onClick={() => this.changeThemeColor("#41cd8c", "#45d0dd")}
            ></div>
            <div
              className="themeColorContainer"
              style={{ background: "linear-gradient(to right, #243949, #517fa4)" }}
              onClick={() => this.changeThemeColor("#243949", "#517fa4")}
            ></div>
            <div
              className="themeColorContainer"
              style={{ background: "linear-gradient(to right, rgba(9,154,151,1), rgba(21,205,168,1))" }}
              onClick={() => this.changeThemeColor("rgba(9,154,151,1)", "rgba(21,205,168,1)")}
            ></div>
            <div
              className="themeColorContainer"
              style={{ background: "linear-gradient(to right, rgba(122,35,52,1), rgba(62,9,27,1))" }}
              onClick={() => this.changeThemeColor("rgba(122,35,52,1)", "rgba(62,9,27,1)")}
            ></div>
            <div
              className="themeColorContainer"
              style={{ background: "linear-gradient(to right, #434343, #000000)" }}
              onClick={() => this.changeThemeColor("#434343", "#000000")}
            ></div>
            <div
              className="themeColorContainer"
              style={{ background: "linear-gradient(to right, #868f96, #596164)" }}
              onClick={() => this.changeThemeColor("#868f96", "#596164")}
            ></div>
            <div
              className="themeColorContainer"
              style={{ background: "linear-gradient(to right, #c71d6f, #fd68a3)" }}
              onClick={() => this.changeThemeColor("#c71d6f", "#fd68a3")}
            ></div>
            <div
              className="themeColorContainer"
              style={{ background: "linear-gradient(to right, #ed6ea0, #ec8c69)" }}
              onClick={() => this.changeThemeColor("#ed6ea0", "#ec8c69")}
            ></div>
          </div>
        </div>
      </div>
    );
  }
}
